/*---Back to top---*/

#buttonBackToTop {
    display: inline-block;
    background-color: #4c8a399c;
    width: 45px;
    height: 45px;
    text-align: center;
    border-radius: 4px;
    position: fixed;
    bottom: 30px;
    right: 15px;
    transition: background-color .3s, opacity .5s, visibility .5s;
    opacity: 0;
    visibility: hidden;
    z-index: 1000;
    border-radius: 10px;
    box-shadow: 1px 1px 20px 4px #4c8a3957;
}

#buttonBackToTop::after {
    content: "\f077";
    font-family: FontAwesome;
    font-weight: normal;
    font-style: normal;
    font-size: 25px;
    line-height: 45px;
    color: #ffffff80;
}

#buttonBackToTop:hover {
    cursor: pointer;
    background-color: var(--primary-bg-color);
}

#buttonBackToTop:hover:after {
    color: #fff;
}

#buttonBackToTop:active {
    background-color: #555;
}

#buttonBackToTop.show {
    opacity: 1;
    visibility: visible;
}


/*---Back to top---*/


/*---Modal---*/


/* The Modal (background) */

.Form__modal {
    display: none;
    position: fixed;
    z-index: 1;
    padding-top: 100px;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgba(0, 0, 0, 0.4);
}

.modal-content {
    background-color: #fefefe;
    margin: auto;
    padding: 20px;
    border: 1px solid #888;
    width: 80%;
}

.close {
    color: #aaaaaa;
    display: block;
    text-align: right;
    font-size: 28px;
    font-weight: bold;
}

.close:hover,
.close:focus {
    color: #000;
    text-decoration: none;
    cursor: pointer;
}


