/*---expanded---*/

@media screen and (max-width: 1024px) {
    .main_menu.main_menu_hide_on_mobile {
        display: none;
    }
    .container {
        max-width: 90%;
    }
    /*Row and divide*/
    .col-divide-lg-12 {
        flex: auto;
        max-width: 100%;
    }
    .col-divide-lg-6 {
        flex: auto;
        max-width: 48%;
    }
    .col-divide-lg-4 {
        flex: auto;
        max-width: 32%;
    }
    .col-divide-lg-3 {
        flex: auto;
        max-width: 24%;
    }
    /*Row and divide*/
    .title--section,
    .title--item {
        text-align: center;
    }
    figure {
        max-width: 100%!important;
    }
}

@media screen and (max-width: 768px) {
    /*Setup*/
    body {
        font-size: 18px;
    }
    .divide--5-col {
        flex: auto;
        max-width: 100%;
    }
    /*Row and divide*/
    .col-divide-md-12 {
        flex: auto;
        max-width: 100%;
    }
    .col-divide-md-6 {
        flex: auto;
        max-width: 48%;
    }
    .col-divide-md-4 {
        flex: auto;
        max-width: 32%;
    }
    .col-divide-md-3 {
        flex: auto;
        max-width: 24%;
    }
    .title--section {
        font-size: 30px;
        margin: 22px 0px;
    }
    /*Row and divide*/
    .dp--none {
        display: none;
    }
    .dp--block {
        display: block;
    }
    .dt--block {
        display: block;
    }
    /*Setup*/
}

@media screen and (max-width: 438px) {
    /*Setup*/
    .title--section {
        font-size: 25px;
        margin: 22px 0px;
    }
    .title--item {
        font-size: 22px;
    }
    /*Row and divide*/
    .col-divide-sm-12 {
        flex: auto;
        max-width: 100%;
    }
    .col-divide-sm-6 {
        flex: auto;
